import React from 'react'

import { i18n } from '../../../i18n'
import { PageLayoutItem } from '../../../molecules/PageLayoutItem'
import { PageLayoutResponsive } from '../../../molecules/PageLayoutResponsive'
import { StockStatisticsButton } from '../../overview/molecules/StockStatisticsButton'
import { MenuStockButton } from '../organisms/MenuStockButton'
import { MenuStockDepositButton } from '../organisms/MenuStockDepositButton'
import { MenuStockTransferButton } from '../organisms/MenuStockTransferButton'

export const StockDashboardPage: React.FC = props => (
  <PageLayoutResponsive
    pageTitle={i18n.t`Stock`}
    showSoundControl={true}
    isForIconDashboard={true}
  >
    <PageLayoutItem
      gridRow={1}
      gridColumn={1}
      gridColumnSpan={2}
      key="MenuStock"
      route="/settings/menu/stock"
      component={MenuStockButton}
      canQuery="stockItems"
    />
    <PageLayoutItem
      gridRow={1}
      gridColumn={3}
      gridColumnSpan={2}
      key="MenuStockDeposit"
      route="/settings/menu/stock/deposit"
      component={MenuStockDepositButton}
      canMutate="changeAmountStockItem"
    />
    <PageLayoutItem
      gridRow={1}
      gridColumn={5}
      gridColumnSpan={2}
      key="MenuStockTransfer"
      route="/settings/menu/stock/transfer"
      component={MenuStockTransferButton}
      canMutate="createStockItemTransfers"
    />
    <PageLayoutItem
      gridColumn={1}
      gridRow={2}
      gridColumnSpan={2}
      key="Stock statistics"
      route="/overview/stock"
      component={StockStatisticsButton}
      canQuery="statistics"
    />
  </PageLayoutResponsive>
)

export default StockDashboardPage
